import React, { Component } from "react";

class Card extends Component {
    constructor(props) {
        super(props);

        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
        this.props.handleClick(this.props.project.header);
    }

    render() {
        let cardClass = this.props.project.size + " card";
        const isFocus = this.props.focus != null;
        cardClass += isFocus ? (this.props.focus == this.props.project.header ? " focus" : " hidden") : " gallery";

        const hasLink = this.props.project.link != null;
        return (
            <div className={ cardClass }>
                
                <div className="card-body" onClick={ this.handleClick } style={{backgroundImage: `url(${this.props.project.img})`}}>
                    <div className="card-overlay"></div>
                    <h2 className="card-header text-header">{  this.props.project.header  }</h2>
                </div>

                <div className="card-desc text-body scale-text-body">
                    <p><i>{ this.props.project.tag }</i></p>
                    <p>{ this.props.project.desc }</p>
                    <p className="card-link">
                        {hasLink
                            ? <a href={ this.props.project.link }>- visit project -</a>
                            : <span className="no-link">- no link yet -</span>
                        }
                    </p>
                </div>
            
            </div>
        )
    }
}

export default Card;